import axios from "axios";
import moment from "moment";
import React, { useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { mainUrl } from "../helpers/api";
import { pickColorStatus } from "../helpers/pickColor";
import Loaded from "../components/Loaded/Loaded";
import LoadedComponent from "../components/Loaded/NoDataSms";
import NoDataSms from "../components/Loaded/NoDataSms";
import { loadSmsMobileSideBarActive } from "../redux/action/utils";
import "./SmsLeads.scss";
import { loadUser } from "../redux/action/auth";

const SmsLeads = ({ auth: { user } }) => {
  const dispatch = useDispatch();
  const [leadsData, setLeadsData] = useState();

  const [loadData, setLoadData] = useState(true);

  

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getLeadsSms = async () => {
      try {
        const res = await axios.get(`${mainUrl}/leads/sms`, config);
        // console.log(res.data.data);
        setLeadsData(res.data.data);
        // console.log(res.data.data);
      } catch (error) {
        console.error(error);
      }
      setLoadData(false);
    };
    getLeadsSms();
  }, []);
  return !loadData && user ? (
    <div className="sms-leads" id="sms-leads">
      {user ? (
        user.data.type !== "SALES_SMS" ? (
          ""
        ) : (
          <Link to={"/sms/leads/add"}>
            <div
              className="d-flex justify-content-center align-items-center"
              style={{
                position: "fixed",
                bottom: "46px",
                right: "30px",
                borderRadius: "50%",
                background: "linear-gradient(180deg, #17949D 0%, #21B8C3 100%)",
                width: "56px",
                height: "56px",
              }}
            >
              <img
                src="/Asset/sms/plus.png"
                alt="plus"
                style={{ width: "24px", height: "24px" }}
              />
            </div>
          </Link>
        )
      ) : (
        ""
      )}

      <div className="sms-header d-flex ">
        <img
          onClick={() => dispatch(loadSmsMobileSideBarActive())}
          src="/Asset/Sidebar/bar.png"
          style={{ width: "23px", height: "23px" }}
          alt="bar"
        />
        <img
          src="/Asset/sms/Logo 1.png"
          style={{ width: "142px", margin: "0 auto" }}
          alt="logo"
        />
      </div>
      {leadsData && leadsData.length !== 0 ? (
        <div>
          <h1
            className="text-center"
            style={{
              fontWeight: "700",
              fontSize: "18px",
              color: "#17949D",
              marginTop: "20px",
            }}
          >
            Lead Lists
          </h1>
          {leadsData ? (
            <div className="bum-table">
              <table className="data" style={{ margin: "10px 0" }}>
                <thead>
                  <tr>
                    <th>Create</th>
                    <th>Last Updated</th>
                    <th>Name</th>
                    <th>Channel </th>
                    <th>Status</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {leadsData.map((lead) => {
                    return (
                      <tr key={lead.id}>
                        <td>{moment(lead.updated_at).format("DD-MMM-YYYY")}</td>
                        <td>{moment(lead.created_at).format("DD-MMM-YYYY")}</td>
                        <td>{lead.customer.first_name}</td>
                        <td>{lead.sms_channel.name}</td>
                        <td style={{ color: pickColorStatus(lead.status) }}>
                          {lead.status}
                        </td>
                        <td>
                          <Link to={`/sms/leads/detail/${lead.id}`}>
                            See Detail
                          </Link>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          ) : (
            <LoadedComponent />
          )}
        </div>
      ) : (
        <NoDataSms />
      )}
    </div>
  ) : (
    <Loaded />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(SmsLeads);
