import React, { Fragment } from "react";
import SmsSidebar from "../Sidebar/SmsSidebar";
import "./Layout.css";

const Layout = (props) => {
  return (
    <Fragment>
      <main className="sms">
        <div className="dashboard-layout-sms">
          <SmsSidebar />
          {props.children}
        </div>
      </main>
    </Fragment>
  );
};

export default Layout;
