import "./App.css";
import {
  BrowserRouter as Router,
  Redirect,
  Route,
  Switch,
} from "react-router-dom";
import LoginSMS from "./pages/LoginSMS";
import SmsRouter from "./pages/SmsRouter";
import SmsRegister from "./pages/SmsRegister";
import setAuthToken from "./utils/setAuthToken";
import { loadUser } from "./redux/action/auth";
import store from "./redux/store/store";
import { useContext, useEffect } from "react";
import Notification from "./components/notification/notification";
import NotificationContext from "./store/notification-context";

if (localStorage.token) {
  setAuthToken(localStorage.token);
}

function App() {
  useEffect(() => {
    store.dispatch(loadUser());
  });

  const notificationCtx = useContext(NotificationContext);

  const activeNotification = notificationCtx.notification;
  return (
    <div>
      {activeNotification && (
        <Notification
          title={activeNotification.title}
          message={activeNotification.message}
          status={activeNotification.status}
        />
      )}
      <Router>
        <Switch>
          <Route path={"/"} exact>
            <Redirect to="/login" />
          </Route>
          <Route path={"/login"} exact component={LoginSMS} />
          <Route path={"/register"} component={SmsRegister} />
          <Route path={"/sms"} component={SmsRouter} />
        </Switch>
      </Router>
      {/* <h1>hello there</h1> */}
    </div>
  );
}

export default App;
