import { SMS_MOBILE_SIDEBAR_ACTIVE, SMS_MOBILE_SIDEBAR_HIDE } from "./types";

export const loadSmsMobileSideBarActive = () => async (dispatch) => {
  dispatch({
    type: SMS_MOBILE_SIDEBAR_ACTIVE,
  });
};

export const loadSmsMobileSideBarHide = () => async (dispatch) => {
  dispatch({
    type: SMS_MOBILE_SIDEBAR_HIDE,
  });
};
