import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import { connect, useDispatch, useSelector } from "react-redux";
import "./Login.css";
import { login } from "../redux/action/auth";
import { Link } from "react-router-dom";

const LoginSMS = ({ auth: { failedLogin } }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();
  const isAuthenticated = useSelector((state) => state.auth.isAuthenticated);

  

  const handleSubmit = async (e) => {
    e.preventDefault();
    const data = {
      email,
      password,
    };

    // console.log(data);
    dispatch(login(data));
    // dispatch(loadTarget());
    setEmail("");
    setPassword("");
  };
  return (
    <section className="gradient-form" style={{ backgroundColor: "#fff" }}>
      {isAuthenticated ? <Redirect to="/sms/leads" /> : ""}
      <div className="">
        <div className="row g-0" style={{ height: "100vh" }}>
          <div className="col-lg-7 d-flex align-items-end align-items-md-center gradient-custom-2 justify-content-center">
            <img
              src="/Asset/Auth Images/login-img.jpg"
              alt="img bg"
              style={{ width: "100%", maxHeight: "100vh", maxWidth: '100%' }}
            />
          </div>
          <div
            className="col-lg-5 login-form d-flex justify-content-center align-items-center"
            style={{
              background: `url('/Asset/Auth Images/background auth2.png')`,
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
            }}
          >
            <div className="card-body p-md-5 mx-md-4 ">
              <div>
                <div className="text-center">
                  <h1
                    style={{
                      color: "#1A9FA9",
                      fontWeight: "700",
                      fontSize: "18px",
                      marginBottom: "16px",
                    }}
                  >
                    Sales Management System
                  </h1>
                  <div className="d-flex justify-content-center align-items-center">
                    <p style={{ color: "#000", fontSize: "10px" }}>
                      Powered By:{" "}
                    </p>
                    <img
                      src="/Asset/Auth Images/MOVES LOGO black.png"
                      alt="logo"
                      className=" ms-2 pb-1 mb-3"
                      style={{ width: "100px" }}
                    />
                  </div>
                </div>

                <form onSubmit={handleSubmit}>
                  {failedLogin && (
                    <div
                      className="alert alert-danger"
                      role="alert"
                      style={{ textAlign: "center" }}
                    >
                      Your email or password is incorrect. <br />
                      Please try again.
                    </div>
                  )}
                  <div className="form-outline mb-4">
                    <input
                      type="email"
                      id="email"
                      className="form-control"
                      name="email"
                      value={email}
                      placeholder="Enter your email"
                      onChange={(e) => setEmail(e.target.value)}
                      required
                    />
                  </div>

                  <div className="form-outline mb-4">
                    <input
                      type="password"
                      id="password"
                      name="password"
                      className="form-control"
                      value={password}
                      placeholder="Enter your password"
                      onChange={(e) => setPassword(e.target.value)}
                      required
                    />
                  </div>

                  <div className="text-center pt-1 mb-2 pb-1">
                    <button
                      className="btn btn-primary btn-block fa-lg gradient-custom-2 mb-3 form-control"
                      type="submit"
                      style={{ backgroundColor: "#006870" }}
                    >
                      Log in
                    </button>
                  </div>
                </form>
                <div className="pb-4 text-center">
                  <p className="mb-0 me-2" style={{ color: "#000" }}>
                    Don't have an account?
                  </p>
                  <p className="mb-0 me-2">
                    <Link
                      to={"/register"}
                      style={{ color: "#006870", fontWeight: "700" }}
                    >
                      Register Now
                    </Link>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(LoginSMS);
