import React, { useEffect } from "react";
import { connect, useDispatch } from "react-redux";
import { Link, Redirect, useLocation } from "react-router-dom";
import { logout } from "../../redux/action/auth";
import { loadSmsMobileSideBarHide } from "../../redux/action/utils";
import "./SmsSidebar.scss";

const SmsSidebar = ({ utils: { smsMobileSideBar } }) => {
  const dispatch = useDispatch();
  //assigning location variable
  const location = useLocation();
  //destructuring pathname from location
  const { pathname } = location;
  const splitLocation = pathname.split("/");

  // const [productBar, setProductBar] = useState(false);
  // const [promoBar, setPromoBar] = useState(false);
  // const [crmBar, setCrmBar] = useState(false);

  // const [mobileSideBar, setMobileSideBar] = useState(false);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const handleLogOut = () => {
    localStorage.removeItem("token");
    // setIsAuthentication(false);
    dispatch(logout());
    return <Redirect to="/login" />;
  };

  const handleMobileSideBar = () => {
    dispatch(loadSmsMobileSideBarHide());
  };
  // console.log(smsMobileSideBar);
  return (
    <nav
      id="sidebarMenu"
      className={`col-md-3 col-lg-2 d-md-block sms-sidebar ${
        smsMobileSideBar ? "mobile-active" : ""
      }`}
    >
      <div className="position-sticky " style={{ paddingTop: "100px" }}>
        {/* <div className="sidebar-logo">
          <img src="/Asset/MOVES LOGO-01 1.png" alt="Logo" />
        </div> */}
        <div
          style={{ position: "absolute", top: "20px", left: "30px" }}
          onClick={() => handleMobileSideBar()}
          className="closeBtn"
        >
          <img
            src="/Asset/Sidebar/times.png"
            alt="times"
            style={{ width: "23px", height: "23px" }}
          />
        </div>
        {/* Dashboard */}
        <ul className="nav flex-column">
          {/* Home */}
          {/* <li className="nav-item ">
            <Link
              className={`nav-link ${
                splitLocation[1] === "dashboard" &&
                splitLocation[2] === undefined
                  ? "active"
                  : ""
              }`}
              aria-current="page"
              to="/dashboard"
            >
              Home
            </Link>
          </li> */}
          {/* Leads */}
          <li className="nav-item ">
            <Link
              className={`nav-link ${
                splitLocation[1] === "dashboard" &&
                splitLocation[2] === undefined
                  ? "active"
                  : ""
              }`}
              aria-current="page"
              to="/sms/leads"
            >
              {/* <img src="/Asset/Sidebar/Dashboard.png" alt="dashboard" /> */}
              Leads
            </Link>
          </li>
          {/* Logout */}
          <li className="nav-item">
            <div
              className={`nav-link ${
                splitLocation[1] === "sign-out" && "active"
              }`}
              aria-current="page"
              style={{ cursor: "pointer" }}
              onClick={handleLogOut}
            >
              {/* <img src="/Asset/Sidebar/Group 1765.png" alt="dashboard" /> */}
              Logout
            </div>
          </li>
        </ul>
      </div>
    </nav>
  );
};
const mapStateToProps = (state) => ({
  utils: state.utils,
});

export default connect(mapStateToProps)(SmsSidebar);
