import { useEffect } from "react";
import { useDispatch } from "react-redux";
import Layout from "../components/Layout/Layout";
import PrivateRouteSms from "../helpers/PrivateRouteSms";
import { loadUser } from "../redux/action/auth";
import SmsAddLead from "./SmsAddLead";
import SmsDeals from "./SmsDeals";
import SmsLeads from "./SmsLeads";
import SmsLeadsDetail from "./SmsLeadsDetail";

const SmsRouter = () => {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(loadUser());
  });
  return (
    <div id="sms">
      <Layout>
        <PrivateRouteSms path={"/sms/leads"} exact component={SmsLeads} />
        <PrivateRouteSms path={"/sms/leads/add"} component={SmsAddLead} />
        <PrivateRouteSms
          path={"/sms/leads/detail/:lead_id"}
          exact
          component={SmsLeadsDetail}
        />
        <PrivateRouteSms
          path={"/sms/leads/detail/:lead_id/deals"}
          component={SmsDeals}
        />
      </Layout>
    </div>
  );
};

export default SmsRouter;
