import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../helpers/api";
import NoDataSms from "../components/Loaded/NoDataSms";
import { loadSmsMobileSideBarActive } from "../redux/action/utils";


const SmsDeals = () => {
  const { lead_id } = useParams();
  const dispatch = useDispatch();

  const [dealsData, setDealsData] = useState([]);



  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };
    const getDeals = async () => {
      try {
        const res = await axios.get(
          `${mainUrl}/leads/deals/${lead_id}`,
          config
        );
        // console.log(res.data.data);
        setDealsData(res.data);
        console.log(res.data);
      } catch (error) {
        console.error(error);
      }
    };

    getDeals();
  }, [lead_id]);

  return (
    <div className="sms-leads" id="sms-leads">
      <Link to={"/sms/leads/add"}>
        <div
          className="d-flex justify-content-center align-items-center"
          style={{
            position: "fixed",
            bottom: "46px",
            right: "30px",
            borderRadius: "50%",
            background: "linear-gradient(180deg, #17949D 0%, #21B8C3 100%)",
            width: "56px",
            height: "56px",
          }}
        >
          <img
            src="/Asset/sms/plus.png"
            alt="plus"
            style={{ width: "24px", height: "24px" }}
          />
        </div>
      </Link>
      <div className="sms-header d-flex ">
        <img
          onClick={() => dispatch(loadSmsMobileSideBarActive())}
          src="/Asset/Sidebar/bar.png"
          style={{ width: "23px", height: "23px" }}
          alt="bar"
        />
        <img
          src="/Asset/sms/Logo 1.png"
          style={{ width: "142px", margin: "0 auto" }}
          alt="logo"
        />
      </div>
      {dealsData.length !== 0 ? (
        <div>
          <h1
            className="text-center"
            style={{
              fontWeight: "700",
              fontSize: "18px",
              color: "#17949D",
              marginTop: "20px",
            }}
          >
            Lead Lists
          </h1>
          <div className="bum-table">
            <table className="data" style={{ margin: "10px 0", width: "100%" }}>
              <thead>
                <tr>
                  <th>No</th>
                  <th>Date</th>
                  <th>Amount </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>1</td>
                  <td>{dealsData ? dealsData.created_at : ""}</td>
                  <td>{dealsData ? dealsData.total_price : ""}</td>
                </tr>
              </tbody>
              {/* <tfoot>
                <tr>
                  <td>Total</td>
                  <td></td>
                  <td>Rp 180.000.000</td>
                </tr>
              </tfoot> */}
            </table>
          </div>
        </div>
      ) : (
        <NoDataSms />
      )}
    </div>
  );
};

export default SmsDeals;
