import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { Link, Redirect, useHistory } from "react-router-dom";

import Loaded from "../components/Loaded/Loaded";
import { mainUrl } from "../helpers/api";
import NotificationContext from "../store/notification-context";
import "./sms.styles.scss";

const SmsRegister = () => {
  const history = useHistory();

  const [name, setName] = useState();
  const [email, setEmail] = useState();
  const [phone, setPhone] = useState();
  const [channel_id, setChannel_id] = useState();
  const [password, setPassword] = useState();

  const [channels, setChannels] = useState();
  const [channelsValidate, setChannelsValidate] = useState(false);

  const [registerDone, setRegisterDone] = useState(false);
  const [error, setError] = useState("");

  const notificationCtx = useContext(NotificationContext);

  const handleSubmit = (e) => {
    e.preventDefault();
    !channel_id && setChannelsValidate(true);

    if (name && email && password && phone && channel_id) {
      notificationCtx.showNotification({
        title: "Signing up...",
        message: "Registering for newsletter.",
        status: "pending",
      });
      const data = {
        name,
        email,
        password,
        phone,
        channel_id,
      };
      register(data);
    }

    // console.log(data);
  };

  const register = async (data) => {
    const config = {
      headers: {
        "Content-Type": "application/json",
      },
    };
    try {
      await axios.post(`${mainUrl}/auth/register`, data, config);
      // console.log(res.data.data);
      setRegisterDone(true);
      history.go(-1);
      notificationCtx.showNotification({
        title: "Success!",
        message: "Successfully registered!",
        status: "success",
      });
    } catch (error) {
      console.error(error);
      console.log(error.response.data.message);
      setError(error.response.data.message);
      notificationCtx.showNotification({
        title: "Error!",
        message: error.message || "Something went wrong!",
        status: "error",
      });
    }
  };

  useEffect(() => {
    const getSmsChannels = async () => {
      try {
        const res = await axios.get(`${mainUrl}/sms-channels`);
        // console.log(res.data.data);
        setChannels(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getSmsChannels();
  }, []);

  return channels ? (
    <div
      className="sms-register"
      id="sms-register"
      style={{ padding: "30px 40px" }}
    >
      <div className="sms-header d-flex ">
        <img
          src="/Asset/sms/Logo 1.png"
          style={{ width: "142px", margin: "0 auto 20px" }}
          alt="logo"
        />
      </div>
      <h1
        className="text-center"
        style={{
          fontWeight: "700",
          fontSize: "18px",
          color: "#17949D",
        }}
      >
        Register
      </h1>
      {registerDone ? (
        <Redirect to={"/sms"} />
      ) : error !== "" ? (
        <div
          className="alert alert-danger"
          role="alert"
          onClick={() => setError("")}
        >
          {error}
        </div>
      ) : (
        ""
      )}
      <form onSubmit={(e) => handleSubmit(e)}>
        <div className="form-outline mb-3">
          <label>
            Full Name <span>*</span>
          </label>
          <input
            type="text"
            id="name"
            className="form-control"
            name="name"
            placeholder="Insert your name"
            onChange={(e) => setName(e.target.value)}
            required
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Email <span>*</span>
          </label>
          <input
            type="email"
            id="email"
            className="form-control"
            name="email"
            placeholder="Insert your email"
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Phone Number <span>*</span>
          </label>
          <input
            type="text"
            id="phone"
            className="form-control"
            name="phone"
            placeholder="Insert your store name"
            onChange={(e) => setPhone(e.target.value)}
            required
          />
        </div>

        <div className="form-outline mb-3">
          <label>
            Store <span>*</span>
          </label>

          <select
            className="form-select"
            aria-label="Default select example"
            onChange={(e) => setChannel_id(parseInt(e.target.value))}
          >
            <option value="">Select Channel</option>
            {channels.map((channel) => (
              <option value={channel.id} key={channel.id}>
                {channel.name}
              </option>
            ))}
          </select>
          {channelsValidate && (
            <div className="alert alert-warning" role="alert">
              Store not selected
            </div>
          )}
        </div>
        <div className="form-outline mb-3">
          <label>
            Create Password <span>*</span>
          </label>
          <input
            type="password"
            id="password"
            className="form-control"
            name="password"
            placeholder="Insert your password"
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <div className="d-flex justify-content-center">
          <button
            style={{
              padding: "10px 35px",
              background: "linear-gradient(90deg, #17949D 0%, #20B5C0 100%)",
              borderRadius: "4px",
              color: "#fff",
              width: "fit-content",
              margin: "0 auto",
            }}
            className="btn"
          >
            Register
          </button>
        </div>
      </form>
      <div className="pb-4 text-center mt-5">
        <p className="mb-0 me-2" style={{ color: "#000" }}>
          If you already have an account, just
        </p>
        <p className="mb-0 me-2">
          <Link to={"/"} style={{ color: "#006870", fontWeight: "700" }}>
            Login
          </Link>
        </p>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

export default SmsRegister;
