import axios from "axios";
import moment from "moment";
import React, { useContext, useEffect, useState } from "react";
import { connect, useDispatch } from "react-redux";
import { Link, useParams } from "react-router-dom";
import { mainUrl } from "../helpers/api";
import { pickColorStatus } from "../helpers/pickColor";
import Loaded from "../components/Loaded/Loaded";
import { loadSmsMobileSideBarActive } from "../redux/action/utils";
import "./sms.styles.scss";
import NotificationContext from "../store/notification-context";

const SmsLeadsDetail = ({ auth: { user } }) => {
  const dispatch = useDispatch();
  const { lead_id } = useParams();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [product_brand_id, setProduct_brand_id] = useState();
  // const [voucher, setVoucher] = useState();
  const [note, setNote] = useState("");

  const [onEdit, setOnEdit] = useState(true);

  const [leadData, setLeadData] = useState();
  // const [addressData, setAddressData] = useState();
  const [brands, setBrands] = useState([]);

  const [updateDone, setUpdateDone] = useState(false);

  const notificationCtx = useContext(NotificationContext);

  useEffect(() => {
    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const getAddress = async (address_id) => {
      try {
        const res = await axios.get(
          `${mainUrl}/addresses/${address_id}`,
          config
        );
        // console.log(res.data.data);
        setAddress(res.data.data.address_line_1);
      } catch (error) {
        console.error(error);
      }
    };
    const getLeadDetails = async () => {
      try {
        const res = await axios.get(`${mainUrl}/leads/sms/${lead_id}`, config);
        // console.log(res.data.data);
        const leadData = res.data.data;
        // console.log(res.data.data);
        // console.log(leadData.product_brand.name);
        setLeadData(leadData);
        setName(leadData.customer.first_name);
        setEmail(leadData.customer.email);
        setPhone(leadData.customer.phone);
        // setAddress(leadData.customer.);
        setNote(leadData.interest);
        setProduct_brand_id(leadData.product_brand.id);
        // console.log(leadData.product_brand.id);
        getAddress(leadData.customer.default_address_id);
        // setVoucher();
        // setNote();
      } catch (error) {
        console.error(error);
      }
    };

    const getBrands = async () => {
      try {
        const res = await axios.get(`${mainUrl}/sms-brands`, config);
        // console.log(res.data.data);
        setBrands(res.data.data);
      } catch (error) {
        console.error(error);
      }
    };

    getLeadDetails();
    getBrands();
  }, [lead_id]);

  useEffect(() => {
    const interval = setInterval(() => {
      setUpdateDone(false);
    }, 3000);
    return () => clearInterval(interval);
  }, [updateDone]);

  const handleSubmitUpdate = async (e) => {
    e.preventDefault();

    const config = {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${localStorage.token}`,
      },
    };

    const data = {
      name,
      email,
      phone,
      address,
      product_brand_id,
      note,
    };
    console.log(data);

    if (name && email && phone && address && product_brand_id) {
      notificationCtx.showNotification({
        title: "Updating Lead...",
        message: "Update Lead.",
        status: "pending",
      });

      try {
        const res = await axios.put(
          `${mainUrl}/leads/update/${lead_id}`,
          data,
          config
        );
        console.log(res.data.data);
        setUpdateDone(true);
        setOnEdit(true);
        notificationCtx.showNotification({
          title: "Success!",
          message: "Successfully Update Lead!",
          status: "success",
        });
      } catch (error) {
        console.error(error);
        notificationCtx.showNotification({
          title: "Error!",
          message: error.message || "Something went wrong!",
          status: "error",
        });
      }
    }
  };

  return leadData ? (
    <div className="sms-leads-detail" id="sms-leads-detail">
      <div className="sms-header d-flex ">
        <img
          onClick={() => dispatch(loadSmsMobileSideBarActive())}
          src="/Asset/Sidebar/bar.png"
          style={{ width: "23px", height: "23px" }}
          alt="bar"
        />
        <img
          src="/Asset/sms/Logo 1.png"
          style={{ width: "142px", margin: "0 auto" }}
          alt="logo"
        />
      </div>
      {/* <h1
        className="text-center"
        style={{ fontWeight: "700", fontSize: "18px", color: "#17949D" }}
      >
        Add Lead
      </h1> */}
      {updateDone && (
        <div className="alert alert-success" role="alert">
          Update Lead Success!
        </div>
      )}
      <div className="d-flex justify-content-between pt-4">
        <div className="text-start">
          <p>Create</p>
          <p>Last Update</p>
          <p>Status</p>
        </div>
        <div className="text-end">
          <p>{moment(leadData.created_at).format("DD MMMM YYYY")}</p>
          <p>{moment(leadData.updated_at).format("DD MMMM YYYY")}</p>
          <p style={{ color: pickColorStatus(leadData.status) }}>
            {leadData.status}
          </p>
        </div>
      </div>
      <hr />
      <form onSubmit={(e) => handleSubmitUpdate(e)}>
        <div className="form-outline mb-3">
          <label>
            Full Name <span>*</span>
          </label>
          <input
            type="text"
            id="name"
            className="form-control"
            name="name"
            placeholder="Insert lead name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            disabled={onEdit}
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Email <span>*</span>
          </label>
          <input
            type="email"
            id="email"
            className="form-control"
            name="email"
            placeholder="Insert your email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            disabled={onEdit}
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Phone Number <span>*</span>
          </label>
          <input
            type="text"
            id="phone"
            className="form-control"
            name="phone"
            placeholder="Insert your phone number"
            value={phone}
            onChange={(e) => setPhone(e.target.value)}
            disabled={onEdit}
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Address <span>*</span>
          </label>
          <input
            type="text"
            id="address"
            className="form-control"
            name="address"
            placeholder="Insert your email"
            value={address}
            disabled={onEdit}
            onChange={(e) => setAddress(e.target.value)}
          />
        </div>
        {/* <select class="form-select" aria-label="Default select example">
          <option selected>{product_brand}</option>
          <option value="1">Product One</option>
          <option value="2">Product Two</option>
          <option value="3">Three</option>
        </select> */}
        {/* <div className="form-outline mb-3">
          <label>
            Brand Recomendation <span>*</span>
          </label>
          <input
            type="text"
            id="brand_recomendation"
            className="form-control"
            name="brand_recomendation"
            placeholder="Insert Brand recomandation"
            value={product_brand}
            disabled={onEdit}
            // onChange={(e) => setProduct_brand(e.target.value)}
          />
        </div> */}
        <div className="form-outline mb-3">
          <label>
            Brand Recomendation <span>*</span>
          </label>
          {/* {!brandValidate && (
            <div className="alert alert-warning" role="alert">
              Brand not selected
            </div>
          )} */}
          <select
            className="form-select"
            name="brand"
            value={product_brand_id}
            onChange={(e) => setProduct_brand_id(parseInt(e.target.value))}
            disabled={onEdit}
          >
            <option value="">Select Brand</option>
            {brands.map((brand) => (
              <option value={brand.id} key={brand.id}>
                {brand.name}
              </option>
            ))}
          </select>
        </div>
        {/* <div className="form-outline mb-3">
          <label>
            Product Type <span>*</span>
          </label>
          <input
            type="text"
            id="productType"
            className="form-control"
            name="productType"
            placeholder="Insert Product type"
            disabled={onEdit}
          />
        </div>
        <div className="form-outline mb-3">
          <label>
            Voucher <span>*</span>
          </label>
          <input
            type="Text"
            id="voucher"
            className="form-control"
            name="voucher"
            placeholder="Select voucher"
            value={voucher}
            onChange={(e) => setVoucher(e.target.voucher)}
            disabled={onEdit}
          />
        </div> */}
        <div className="form-outline mb-3">
          <label>
            Notes<span>*</span>
          </label>
          <textarea
            type="text"
            id="note"
            className="form-control"
            name="note"
            placeholder="Insert notes"
            value={note}
            onChange={(e) => setNote(e.target.value)}
            disabled={onEdit}
          />
        </div>
        {!onEdit && (
          <button
            style={{
              padding: "10px 0",
              background: "linear-gradient(90deg, #17949D 0%, #20B5C0 100%)",
              borderRadius: "4px",
              color: "#fff",
              width: "100%",
              textAlign: "center",
              marginBottom: "20px",
            }}
            className="btn"
          >
            Update
          </button>
        )}
      </form>
      <div className="d-flex justify-content-between">
        {user
          ? user.data.type === "SALES_SMS" && (
              <div
                onClick={() => setOnEdit(!onEdit)}
                style={{
                  padding: "10px 0",
                  border: "1px solid #17949D",
                  borderRadius: "4px",
                  color: "#17949D",
                  width: "45%",
                  textAlign: "center",
                }}
              >
                Edit
              </div>
            )
          : ""}

        <Link
          to={`/sms/leads/detail/${lead_id}/deals`}
          style={{
            padding: "10px 0",
            background: "linear-gradient(90deg, #17949D 0%, #20B5C0 100%)",
            borderRadius: "4px",
            color: "#fff",
            width: "45%",
            textAlign: "center",
          }}
        >
          See Deals
        </Link>
      </div>
    </div>
  ) : (
    <Loaded />
  );
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps)(SmsLeadsDetail);
